@import url(https://fonts.googleapis.com/css2?family=Cormorant:wght@400;700&display=swap);
.header {
  font-weight: lighter;
  display: block;
  text-align: center;
  font-size: 60px;
  font-family: "Cormorant", sans-serif;
}

.form-row {
  display: flex;
  justify-content: space-evenly;
}

.form-column {
  text-align: center;
}

.commentSection {
  display: flex;
  justify-content: center;
}

textarea.textbox {
  width: 800px;
  height: 200px;
  margin-top: 3rem;
}

.postButton,
.getUpdatedMarketPrices {
  display: flex;
  margin: 3rem;
  justify-content: center;
}

.inputTickerSymbol {
  width: 90px;
}

.holdingsTable {
  border: solid 1px rgb(175, 166, 166);
  border-collapse: collapse;
  border-spacing: 0;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
  font-size: 20px;
}

.holdingsTable thead th {
  background-color: rgb(110, 100, 100);
  border: solid 1px rgb(255, 255, 255);
  color: rgb(255, 255, 255);
  padding: 10px;
  text-align: left;
}

.holdingsTable tbody td {
  color: #333;
  padding: 10px;
}

.trow:hover,
.btn {
  cursor: pointer;
}

.btn:hover {
  background-color: rgb(255, 255, 255);
}

.trow:hover {
  background-color: rgb(226, 226, 226);
}

.price,
.roi {
  text-align: center;
}

header,
.briefOV,
.appDesc,
.signUpForm {
  position: center;
  text-align: center;
  margin-left: 25px;
  margin-right: 25px;
}

header {
  font-size: 20px;
}

.landingPage {
  font-size: 20px;
}

.join_button {
  margin: 10px;
}

input {
  transition: width 0.4s ease-in-out;
  margin-bottom: 10px;
}

input:focus {
  border: 3px solid #555;
}

label {
  margin-right: 20px;
}

/**
 *  Tablet Vertical

@media screen and (min-width: 620px) {
  header {
    font-size: 25px;
    margin: 75px;
  }
  button {
    padding: 15px;
    font-size: 18px;
    font-weight: bold;
  }
}

/**
 *  Tablet Horizontal
 
@media screen and (min-width: 960px) {
  header h1 {
    margin-bottom: 7rem;
  }

  .briefOV,
  .appDesc,
  .signUpForm {
    margin-top: 10rem;
    margin-bottom: 20rem;
  }

  .briefOV h3,
  .appDesc h3,
  .signUpForm h3 {
    font-size: 30px;
  }

  .briefOV p,
  .appDesc p,
  .signUpForm p {
    font-size: 20px;
  }
  input {
    transition: width 0.4s ease-in-out;
    margin-left: 400px;
    margin-bottom: 15px;
  }
  .signup-form {
    text-align: left;
    margin-left: 100px;
  }
}

/**
 *  Desktop
 
@media screen and (min-width: 1200px) {
}
*/

.edit-position {
  font-weight: lighter;
  display: block;
  text-align: center;
  font-size: 60px;
  font-family: "Cormorant", sans-serif;
}

.formContainer {
  margin: 4rem;
}

